@import "../utils/utils.less";

.page-body {

  @media (min-width: @mobile-width-only) {
    width: 100%;
    max-width: 100%;
    min-width: 320px;
    min-height: 100%;
    margin: 0 auto;
    padding: 0;
    font-family: "Blogger Sans", "Arial", sans-serif;
  }

  @media (min-width: @tablet-width) {
    width: 100%;
    max-width: 100%;
    min-width: 768px;
    min-height: 100%;
    margin: 0 auto;
    padding: 0;
    font-family: "Blogger Sans", "Arial", sans-serif;
  }

  @media (min-width: @desktop-width) {
    width: 100%;
    max-width: 100%;
    min-width: 1440px;
    min-height: 100%;
    margin: 0 auto;
    padding: 0;
    font-family: "Blogger Sans", "Arial", sans-serif;
  }
}
